import React, { useState } from "react";
import CoordinateInput from "./components/CoordinateInput";
import Board3D from "./components/Board3D";
import { choreo } from "./utils/data";

const App = () => {
  const [choreographie, setChoreographie] = useState(choreo);
  const [rotateScene, setRotateScene] = useState(false);

   // Aktualisiert die Position eines Tänzers in einem bestimmten Bild
   const handleUpdate = (taenzerIndex, bildIndex, newCoords) => {
    const updatedChoreographie = { ...choreographie };
    updatedChoreographie.taenzer[taenzerIndex].bilder[bildIndex] = newCoords;
    setChoreographie(updatedChoreographie);
  };

  return (
    <div className="app-container">
    <div className="sidebar">
      <h2>{choreographie.name}</h2>
      {choreo.taenzer.map((taenzer, taenzerIndex) => (
        <CoordinateInput
          key={taenzerIndex}
          taenzer={taenzer}
          taenzerIndex={taenzerIndex}
          onUpdate={handleUpdate}
          rotateScene={rotateScene}
          setRotateScene={setRotateScene}
        />
      ))}
    </div>
    <div className="board-container">
      <Board3D choreo={choreographie} rotateScene={rotateScene} />
    </div>
  </div>
  );
};

export default App;
