import React, { useEffect, useRef } from "react";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";

const Board3D = ({ choreo, rotateScene }) => {
  const mountRef = useRef(null);
  const sceneRef = useRef(null);
  const rendererRef = useRef(null);
  const cameraRef = useRef(null);
  const spheresRef = useRef([]); // Referenz für die Kugeln

  useEffect(() => {
    // Szene, Kamera und Renderer erstellen
    const scene = new THREE.Scene();
    sceneRef.current = scene;

    const camera = new THREE.PerspectiveCamera(75, 1, 0.1, 1000);
    camera.position.set(0, 10, 15);
    cameraRef.current = camera;

    const renderer = new THREE.WebGLRenderer({ antialias: true });
    renderer.setSize(800, 800); // Szene auf 800x800 setzen
    renderer.setClearColor("#1f1f1f"); // Hintergrundfarbe setzen
    rendererRef.current = renderer;

    // Renderer dem DOM hinzufügen
    if (mountRef.current) {
      mountRef.current.appendChild(renderer.domElement);
    }

    // OrbitControls für Kamerasteuerung
    const controls = new OrbitControls(camera, renderer.domElement);
    controls.enableDamping = true;

    // Lichtquellen hinzufügen
    const ambientLight = new THREE.AmbientLight(0x404040, 2); // Umgebungslicht
    scene.add(ambientLight);

    const pointLight = new THREE.PointLight(0xffffff, 1.5); // Punktlicht
    pointLight.position.set(10, 10, 10);
    scene.add(pointLight);

    // Boden (Gitter)
    const gridHelper = new THREE.GridHelper(16, 16, 0x0000ff, 0x808080);
    scene.add(gridHelper);

    // Animation
    const animate = () => {
      requestAnimationFrame(animate);

      if (rotateScene) {
        scene.rotation.y += 0.01; // Szene dreht sich um die Y-Achse
      }

      controls.update();
      renderer.render(scene, camera);
    };

    animate();

    // Cleanup-Funktion
    return () => {
      // Renderer entfernen
      if (mountRef.current && renderer.domElement) {
        mountRef.current.removeChild(renderer.domElement);
      }

      // Alle Objekte der Szene entfernen
      scene.traverse((object) => {
        if (object.isMesh) {
          object.geometry.dispose();
          object.material.dispose();
        }
      });

      renderer.dispose();
    };
  }, []); // Dieser useEffect wird nur beim ersten Mount ausgeführt

  // Aktualisiere die Kugeln, wenn choreo sich ändert oder rotateScene aktiv ist
  useEffect(() => {
    const scene = sceneRef.current;

    // Entferne bestehende Kugeln
    spheresRef.current.forEach((sphere) => {
      scene.remove(sphere);
    });

    // Leere die Referenz
    spheresRef.current = [];

    // Neue Kugeln basierend auf choreo hinzufügen
    choreo.taenzer.forEach((taenzer) => {
      taenzer.bilder.forEach((bild) => {
        const sphereGeometry = new THREE.SphereGeometry(0.3, 32, 32);
        const sphereMaterial = new THREE.MeshBasicMaterial({ color: 0x08fdd8 }); // Türkis wie Überschrift
        const sphere = new THREE.Mesh(sphereGeometry, sphereMaterial);
        sphere.position.set(bild.x, 0.5, bild.y);
        scene.add(sphere);
        spheresRef.current.push(sphere);
      });
    });

    // Kugeln müssen auch bei Rotation neu gerendert werden
    rendererRef.current.render(scene, cameraRef.current);

  }, [choreo, rotateScene]); // Aktualisiert Kugeln, wenn choreo oder rotateScene geändert wird

  return <div ref={mountRef}></div>;
};

export default Board3D;