import React from "react";

const CoordinateInput = ({ taenzer, taenzerIndex, onUpdate, rotateScene, setRotateScene }) => {
  const handleChange = (bildIndex, axis, value) => {
    const newCoords = {
      ...taenzer.bilder[bildIndex],
      [axis]: parseFloat(value),
    };
    onUpdate(taenzerIndex, bildIndex, newCoords);
  };

  return (
    <div className="coordinate-input">
       {/* Checkbox für Szene rotieren */}
       {taenzerIndex === 0 && (
        <div style={{ marginBottom: "1rem", textAlign: "left" }}>
          <label>
            <input
              type="checkbox"
              checked={rotateScene}
              onChange={(e) => setRotateScene(e.target.checked)}
            />
            Szene rotieren
          </label>
        </div>
      )}

      {taenzer.bilder.map((bild, bildIndex) => (
        <div key={bildIndex} className="input-row">
          <div className="taenzer-info">
            <span className="taenzer-name">{`(${taenzerIndex + 1}) ${taenzer.name}`}</span>
          </div>
          <div className="input-group">
            <label className="axis-label">X:</label>
            <input
              type="number"
              value={bild.x}
              onChange={(e) => handleChange(bildIndex, "x", e.target.value)}
            />
          </div>
          <div className="input-group">
            <label className="axis-label">Y:</label>
            <input
              type="number"
              value={bild.y}
              onChange={(e) => handleChange(bildIndex, "y", e.target.value)}
            />
          </div>
        </div>
      ))}

     
    </div>
  );
};

export default CoordinateInput;
